import axios from 'axios';

const proxy = 'http://project-task-system.herokuapp.com';

class Service {
    getProjects() {
        return axios.get(proxy + `/api/projects`);
    }
    getProject(projectId) {
        return axios.get(proxy + `/api/projects/${projectId}`);
    }
    deleteProject(projectId) {
        return axios.delete(proxy + `/api/projects/${projectId}`);
    }
    updateProject(projectId, project) {
        return axios.put(proxy + `/api/projects/${projectId}`, project);
    }
    createProject(project) {
        return axios.post(proxy + `/api/projects`, project);
    }
    updateProjectCompleted(projectId) {
        return axios.patch(proxy + `/api/projects/${projectId}/completed`)
    }
    // Task axios 
    getTasksByProjectId(projectId) {
        return axios.get(proxy + `/api/projects/${projectId}/tasks`);
    }
    getTasks() {
        return axios.get(proxy + `/api/projects/tasks`);
    }
    getTaskByTaskId(taskId) {
        return axios.get(proxy + `/api/projects/tasks/${taskId}`);
    }
    getTasksInProgress() {
        return axios.get(proxy + `/api/projects/tasks/inprogress`);
    }
    getTasksTodo() {
        return axios.get(proxy + `/api/projects/tasks/todo`);
    }
    getTasksDone() {
        return axios.get(proxy + `/api/projects/tasks/done`);
    }
    addTask(projectId, task) {
        return axios.post(proxy + `/api/projects/${projectId}/tasks`, task);
    }
    deleteTask(taskId) {
        return axios.delete(proxy + `/api/projects/tasks/${taskId}`);
    }
    updateTask(taskId, task) {
        return axios.put(proxy + `/api/projects/tasks/${taskId}`, task)
    }
    updateTaskInprogress(taskId) {
        return axios.patch(proxy + `/api/projects/tasks/${taskId}/inprogress`)
    }
    updateTaskDone(taskId) {
        return axios.patch(proxy + `/api/projects/tasks/${taskId}/done`)
    }
    updateTaskTodo(taskId) {
        return axios.patch(proxy + `/api/projects/tasks/${taskId}/todo`)
    }
    searchProject(search) {
        return axios.get(proxy + `/api/projects/searchproject?search=${search}`)
    }
    searchTask(search) {
        return axios.get(proxy + `/api/projects/searchtask?search=${search}`)
    }
    exportProjects() {
        return axios.get(proxy + `/api/projects/exportProjects`);
    }
    exportTasks(projectId) {
        return axios.get(proxy + `/api/projects/exportTasks/${projectId}`);
    }

}

export default new Service()